import { useEffect, useRef } from 'react'
import styles from './Box.module.css'

const Box = () => {
	const boxRef = useRef(null)

	useEffect(() => {
		setInterval(() => {
			boxRef.current = styles.red
		}, 1000)
	}, [boxRef])

	return <div ref={boxRef}> </div>
}

export default Box
